/* Hero Section */
.hero-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50vh; /* Keeping the reduced height */
  padding: 40px 8%;
  background-color: #fff;
  gap: 20px; /* Reduce the gap between text and image */
}

/* Left Side Text */
.hero-text {
  flex: 1;
  max-width: 50%; /* Allow text to take a bit more space */
  margin-right: 10px; /* Reduce space between text and image */
}

.business-name {
  font-size: 60px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #000;
}

.hero-tagline {
  font-size: 17px;
  color: #555;
  margin-bottom: 20px;
  max-width: 450px; /* Adjust for better fit */
}

/* Buttons */
.hero-buttons {
  display: flex;
  gap: 15px;
}

.whatsapp-btn,
.secondary-btn,
.primary-btn {
  padding: 12px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

/* Individual Button Styles */
.whatsapp-btn {
  background-color: #25D366;
  color: white;
  border: none;
}

.secondary-btn {
  background-color: transparent;
  color: black;
  border: 2px solid black;
}

.primary-btn {
  background-color: black;
  color: white;
  border: none;
}

/* Hover Effects */
button:hover {
  transform: scale(1.05);
  opacity: 0.9;
}

.whatsapp-btn:hover {
  background-color: #1da851;
}

.secondary-btn:hover {
  background-color: black;
  color: white;
}

.primary-btn:hover {
  background-color: #333;
}

/* Right Side Image */

.hero-image {
  flex: 1.5; /* Keeps the image space */
  display: flex;
  justify-content: flex-start; /* Moves the image towards the left */
  align-items: center;
  padding-left: 20px; /* Adjust left padding to fine-tune positioning */
}

.hero-image img {
  width: 100%;
  max-width: 620px; /* Adjust size if needed */
  height: auto;
  border-radius: 10px;
}
/* Hero Video Container */
.hero-video-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  opacity: 0;
  animation: fadeIn 1.6s ease-in-out forwards 0.7s; /* Delayed fade-in */
}

/* Video Styling */
/* Hero Video Container */
.hero-video-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  opacity: 0;
  animation: fadeIn 1.6s ease-in-out forwards 0.7s; /* Delayed fade-in */
}

/* Video Styling */
.hero-video {
  width: 80%; /* Standard width */
  max-width: 900px; /* Keep it limited for balance */
  height: 450px; /* Standard height */
  object-fit: cover; /* Keeps video aspect ratio intact */
  border-radius: 10px; /* Soft rounded corners */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  display: block;
  margin: 20px auto; /* Centers the video */
}
/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}